  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  background-color: #FFFFCC;
  padding-bottom: 50px;
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}
.btn {
  cursor: pointer;
  color: #42b983;
}



